import axios from "axios";
import {call, put} from "redux-saga/effects";
import {DATA_POST} from '../actions/types';
import {getFromStorage} from '/Users/santoshdahal/Documents/cash-the-trash/client/src/Util/storage.js';

export function *displayData(){
    const obj = getFromStorage('the_main_app');
    if (obj&&obj.token){
      try{
        const response = yield call (axios.get,'/showProduct');
        yield put({
          type: DATA_POST,
          payload: response.data.data

        });
        response.data.data.map((data,index) =>{
          if (data.quantity === 0) {
            function *soldProduct(){
              try{
                const response = yield call(axios.post, '/soldProductLog',{
                  productName:data.productName,
                  productPrice:data.productPrice,
                  productDescription:data.productDescription,
                  quantity:data.quantity,
                  productId:data.productId,
                });
              }catch(e){

              }
            }
          }
        }
      )
    }catch(e){

      }
    }
  }



