import React from 'react';
import {getFromStorage,setInStorage} from '/Users/santoshdahal/Documents/cash-the-trash/client/src/Util/storage.js';
import {Button} from './Button.js';
import {Link,Redirect} from 'react-router-dom';
import LogInPage from './LogInPage';
import Top from './Top'
import {connect} from 'react-redux';
import {displayData} from '../sagas/displayPageSaga';

class DisplayPage extends React.Component{
  componentWillMount(){
    displayData();
  }

  render(){
    const {
      productName,
      productPrice,
      productDescription,
      productId,
      quantity,
      token,
      data
    } = this.props;

    const post = {
      productName,
      productPrice,
      productDescription,
      productId,
      quantity,
      token,
    }

      if(!token){
        return(
          <div>
            <Redirect to = "/"/>
          </div>
        )
      }else{
        const dataList = data.map((name,i) =>{
          if(name.quantity !== 0){
            return(
              <div key = {'data_'+i}>
                Name:{name.productName}<br/>
                Price:${name.productPrice}<br/>
                Description:{name.productDescription}<br/>
                quantity:{name.quantity}<br/>
                <Button name = {name.productName} price = {name.productPrice} description = {name.productDescription}/>
                <br/><br/><br/>
              </div>
            )
          }
        }
      );
      return(
        <div className = 'displayPage'>
          <div>
            <Top/>
          </div>
          <div>
          Welcome to my site.Turn your trash into cash.
          </div>

          <div>
            <br/>
            <br/>
            <br/>
            Her are some cool products so far.
          </div>

          <div>
            {dataList}
          </div>
        </div>
      )
    }
  }
}

const mapStatesToProps = state => ({
  token: state.login.token,
  data: state.displayPage.data,
})

export default connect(mapStatesToProps,{displayData})(DisplayPage);



