import axios from "axios";
import {call, put} from "redux-saga/effects";
import {STORAGE_POST,ISLOADING_POST} from '../actions/types.js';
import {getFromStorage} from '/Users/santoshdahal/Documents/cash-the-trash/client/src/Util/storage.js';

export function *getTokenAction () {
  const obj = getFromStorage('the_main_app');
  if(obj && obj.token){
    const token = obj.token;
    try {
      const response = yield call(axios.get,'/verify?token='+token);
      yield put({type: STORAGE_POST, payload: obj.token});
      yield put({type:ISLOADING_POST, payload: false });
    } catch (e) {
    }
  }else{
    yield put({type:ISLOADING_POST, payload: false });
  }
}



