import axios from "axios";
import {call, takeEvery} from "redux-saga/effects";
import {setInStorage} from '/Users/santoshdahal/Documents/cash-the-trash/client/src/Util/storage.js';
import {REQUEST_LOGIN_ACTION} from '../actions/types';

export function *logInActions(postData){
  function refreshPage(){
    window.location.reload();
  }

  try {
    const response = yield call(axios.post,'/logIn',
    {
      userName: postData.payload.logInUserName,
      password: postData.payload.logInPassword
    });
    if(response.status === 200){
      setInStorage('the_main_app',{token:response.data.id});
      refreshPage();
    }
  } catch (e) {

  }

}

export function* requestLogInActions(){
  yield takeEvery(REQUEST_LOGIN_ACTION, logInActions);
}



